<template>
  <div id="detection-field">
    <img src="../../../assets/images/title_jcbgtbfx.png">

    <!-- <Echart id="DetectionField" ref="DetectionFieldRef" :options="options" height="240px" width="600px" /> -->
    <dv-loading v-if="Loading" style="">数据加载中...</dv-loading>
    <div v-show="!Loading" id="DetectionField" ref="DetectionFieldRef" style="width: 600px;height:240px">
    </div>
  </div>
</template>

<script>
// import Echart from '@/common/echart'

export default {
  data() {
    return {
      options: {},
      Loading: true,
    };
  },
  components: {
    // Echart,
  },
  mounted() {},
  props: {
    NewTrend: {
      type: Array,
      require: true,
      default: () => {},
    },
    OldTrend: {
      type: Array,
      require: true,
      default: () => {},
    },
  },

  watch: {
    OldTrend: {
      handler(val) {
        if (val && val[0]) {
          this.Loading = false;
          this.initChart();
        }
      },
      immediate: true,
      deep: true,
    },
  },

  methods: {
    initChart() {
      var myChart = this.$echarts.init(
        document.getElementById("DetectionField")
      );
      var option = {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: "shadow", // 默认为直线，可选为：'line' | 'shadow'
          },
        },
        grid: {
          left: "2%",
          right: "4%",
          bottom: "14%",
          top: "16%",
          containLabel: true,
        },
        legend: {
          data: ["去年", "今年"],
          right: 10,
          top: 12,
          textStyle: {
            color: "#fff",
          },
          itemWidth: 12,
          itemHeight: 10,
          // itemGap: 35
        },
        xAxis: {
          type: "category",
          data: [
            "1月",
            "2月",
            "3月",
            "4月",
            "5月",
            "6月",
            "7月",
            "8月",
            "9月",
            "10月",
            "11月",
            "12月",
          ],
          axisLine: {
            lineStyle: {
              color: "#A9B1B6",
            },
          },
          axisLabel: {
            // interval: 0,
            // rotate: 40,
            textStyle: {
              fontFamily: "Microsoft YaHei",
            },
          },
        },

        yAxis: {
          type: "value",
          // max: '1200',
          axisLine: {
            show: false,
            lineStyle: {
              color: "white",
            },
          },
          splitLine: {
            show: true,
            lineStyle: {
              color: "rgba(255,255,255,0.3)",
            },
          },
          axisLabel: {},
        },

        series: [
          {
            name: "去年",
            type: "bar",
            barWidth: "15%",
            itemStyle: {
              color: {
                type: "linear",
                // x=0,y=1,柱子的颜色在垂直方向渐变
                x: 0,
                y: 1,
                colorStops: [
                  // 0%处的颜色
                  {
                    offset: 0,
                    color: "#00806A",
                  },
                  // 100%处的颜色
                  {
                    offset: 1,
                    color: "#9CFDED",
                  },
                ],
                global: false, // 缺省为 false
              },
            },
            data: this.OldTrend, //[56, 343, 877, 543, 222, 111, 233, 400, 300, 545, 121, 331]
          },
          {
            name: "今年",
            type: "bar",
            barWidth: "15%",
            itemStyle: {
              color: {
                type: "linear",
                x: 0,
                y: 1,
                colorStops: [
                  // 0%处的颜色
                  {
                    offset: 0,
                    color: "#045688",
                  },
                  // 100%处的颜色
                  {
                    offset: 1,
                    color: "#5DC2FF",
                  },
                ],
                global: false, // 缺省为 false
              },
            },
            data: this.NewTrend, //[121, 555, 454, 233, 500, 400, 321, 1212, 221, 903, 888, 767]
          },
        ],
      };

      var app = {
        currentIndex: -1,
      };
      setInterval(function () {
        var dataLen = option.series[0].data.length;
        // 取消之前高亮的图形
        myChart.dispatchAction({
          type: "downplay",
          seriesIndex: 0,
          dataIndex: app.currentIndex,
        });
        app.currentIndex = (app.currentIndex + 1) % dataLen;
        //console.log(app.currentIndex);
        // 高亮当前图形
        myChart.dispatchAction({
          type: "highlight",
          seriesIndex: 0,
          dataIndex: app.currentIndex,
        });
        // 显示 tooltip
        myChart.dispatchAction({
          type: "showTip",
          seriesIndex: 0,
          dataIndex: app.currentIndex,
        });
      }, 2000);

      if (option) {
        myChart.setOption(option);
      }
    },
  },
};
</script>

<style lang="scss" scoped>
#detection-field {
  width: 616px;
  height: 289px;
  // background: url("~@/assets/images/title_ywzl.png") no-repeat;
  background: rgba($color: #072036, $alpha: 0.46);
  padding-top: 17px;
  border-top: 3px solid;
  border-image: linear-gradient(to left, #052348, rgba(171, 218, 255, 0.58)) 1;
}
</style>
