<template>
  <div id="detection-field">
    <img src="../../../assets/images/title_jclybgzb.png">
    <!-- <Echart id="DetectionFieldChart" ref="DetectionFieldRef" :options="options" height="440px" width="566px" /> -->
    <div class="report-statics-body">
      <dv-loading v-if="Loading" style="">数据加载中...</dv-loading>
      <dv-scroll-ranking-board v-else :config="config" style="width: 520px; height: 440px;" :key="randomKey" />
    </div>
  </div>
</template>

<script>
// import Echart from '@/common/echart'
// import echartImg from "@/assets/images/icon_jiancelingyu.png"

export default {
  data() {
    return {
      config: {
        data: [],
        rowNum: 10,
        waitTime: 3000,
        valueFormatter({ value }) {
          return (value * 100).toFixed(2) + "%";
        },
      },
      unit: "%",
      randomKey: Math.random(),
      Loading: true,
    };
  },
  components: {
    // Echart,
  },

  props: {
    ApiData: {
      type: Array,
      require: true,
      default: () => {},
    },
  },

  watch: {
    ApiData: {
      handler(val) {
        if (val && val[0]) {
          this.config.data = val.filter((item) => item.value > 0.0001);
          this.randomKey = Math.random(); //刷新组件
          this.Loading = false;
        }
      },
      immediate: true,
      deep: true,
    },
  },

  methods: {},
};
</script>

<style lang="scss" scoped>
#detection-field {
  width: 566px;
  height: 512px;
  // background: url("~@/assets/images/title_ywzl.png") no-repeat;
  background: rgba($color: #072036, $alpha: 0.46);
  padding-top: 17px;
  border-top: 3px solid;
  border-image: linear-gradient(to left, #052348, rgba(171, 218, 255, 0.58)) 1;

  .report-statics-body {
    height: 480px;
    padding: 10px 10px 10px 20px;
  }
}

::v-deep .rank {
  color: #e9e9e9 !important;
}

// ::v-deep .dv-scroll-ranking-board .ranking-column .inside-column {
//   background: linear-gradient(270deg, rgba(15, 66, 115, 1), rgba(93, 194, 255, 1));
// }

::v-deep
  .dv-scroll-ranking-board
  .row-item:nth-child(odd)
  > .ranking-column
  .inside-column {
  background: linear-gradient(
    270deg,
    rgba(15, 66, 115, 1),
    rgba(93, 194, 255, 1)
  );
  // background: linear-gradient(to right, rgba(15, 66, 115, 1) 55%, #002947 0, rgba(15, 66, 115, 1) 60%, #002947 0, rgba(15, 66, 115, 1) 70%, #002947 0);
  // background-size: 20px 20px;
}

::v-deep
  .dv-scroll-ranking-board
  .row-item:nth-child(even)
  > .ranking-column
  .inside-column {
  background: linear-gradient(
    270deg,
    rgba(115, 75, 15, 1),
    rgba(255, 172, 93, 1)
  );
  // background-size: 20px 20px;
}

::v-deep .dv-scroll-ranking-board .ranking-column {
  background-color: #002947;
  border-bottom: 0;
}
</style>
