<template>
  <div id="detection-users">
    <img src="../../../assets/images/title_jcry.png">
    <div class="users-body">
      <div class="data-item" @click="handleClick(0)">
        <img src="../../../assets/images/icon_jiancerenyuan_1.png" class="users-img" />
        <div class="users-info">
          <span>检测人员</span>
          <div>
            <span class="users-num-blue">
              {{educationValue.personnel}}
              <!-- <CountTo :startVal='startVal' :endVal="TesterCount" :duration='duration' separator="," /> -->
            </span>人
          </div>
        </div>
      </div>
      <div class="data-item">
        <img src="../../../assets/images/icon_nianling.png" class="users-img" />
        <div class="users-info">
          <span>平均年龄</span>
          <div>
            <span class="users-num-green">
              <CountTo :startVal='startVal' :endVal=30 :duration='duration' separator="," />
            </span>岁
          </div>
        </div>
      </div>
      <div class="data-item">
        <img src="../../../assets/images/icon_zhengshu.png" class="users-img" />
        <div class="users-info">
          <span>注册证书量</span>
          <div>
            <span class="users-num-blue">
              <CountTo :startVal='startVal' :endVal=713 :duration='duration' separator="," />
            </span>个
          </div>
        </div>

      </div>
      <div class="data-item"  @click="handleClick(1)">
        <img src="../../../assets/images/icon_benke.png" class="users-img" />
        <div class="users-info">
          <span>本科学历</span>
          <div>
            <span class="users-num-green">
              {{educationValue.bachelorDegree}}
              <!-- <CountTo :startVal='startVal' :endVal=689 :duration='duration' separator="," /> -->
            </span>人
          </div>
        </div>

      </div>
      <div class="data-item"  @click="handleClick(2)">
        <img src="../../../assets/images/icon_yanjiusheng.png" class="users-img" />
        <div class="users-info">
          <span>研究生学历</span>
          <div>
            <span class="users-num-blue">
              {{educationValue.graduateStudent}}
              <!-- <CountTo :startVal='startVal' :endVal=96 :duration='duration' separator="," /> -->
            </span>人
          </div>
        </div>

      </div>
      <div class="data-item"  @click="handleClick(3)">
        <img src="../../../assets/images/icon_boshi.png" class="users-img" />
        <div class="users-info">
          <span>博士及以上学历</span>
          <div>
            <span class="users-num-green">
              {{educationValue.doctorate}}
              <!-- <CountTo :startVal='startVal' :endVal=4 :duration='duration' separator="," /> -->
            </span>人
          </div>
        </div>
      </div>
    </div>
    <el-drawer title="详情" :visible.sync="drawerVisible" size="1000px" append-to-body modal-append-to-body @close="() => {
      this.query = Object.assign({}, this.initQuery)
    }">
      <dv-loading v-if="Loading">数据加载中...</dv-loading>
      <div v-else>
        <el-table ref="table" :data="tableValue" tooltip-effect="dark" style="width: 100%;">
          <el-table-column label="序号" type="index" width="70"></el-table-column>
          <el-table-column label="姓名" prop="userName" />
          <el-table-column label="工号" prop="userNumber" />
          <el-table-column label="岗位职务" prop="positions" />
          <el-table-column label="部门" prop="deptNames" show-overflow-tooltip/>
          <el-table-column label="最高学历" prop="educationName" />
        </el-table>
        <div class="table-pagination">
          <div class="data-num">共<span> {{ query.total }} </span>条数据</div>
          <el-pagination :current-page="query.pageIndex" :page-size="query.pageSize" :total="query.total" background layout="sizes, prev, pager, next, jumper" @size-change="handleSizeChange" @current-change="paginationCurrentChange" />
        </div>
      </div>
    </el-drawer>
  </div>
</template>

<script>
import { getEducationCount, getUserPage } from "@/api/detection.js"
import CountTo from "vue-count-to"
export default {
  data() {
    this.initQuery = {
      pageIndex: 1,
      pageSize: 10,
      total: 0,
    }
    return {
      Loading: false,
      startVal: 0,
      duration: 1000,
      query: Object.assign({}, this.initQuery),
      drawerVisible: false,
      tableValue: [],
      index: 0,
      educationValue: {
        bachelorDegree: "",
        graduateStudent: "",
        doctorate: "",
        personnel:""
      }
    }
  },
  components: {
    CountTo
  },
  props: ["TesterCount"],
  mounted() {
    this.getData()
  },
  methods: {
    getData() {
      getEducationCount().then(res => {
        if (res.code == 0) {
          this.educationValue = res.data
        }
      })
    },
    toQuery() {
      getUserPage(this.query).then(res => {
        if (res.code == 0) {
          this.Loading = false
          this.tableValue = res.data.records
          this.query.total = res.data.total
        }
      }).finally(() => {
        this.Loading = false
      })
    },
    paginationCurrentChange(pageIndex) {
      this.query = { ...this.query, pageIndex: pageIndex }
      this.toQuery()
    },
    handleSizeChange(pageSize) {
      this.query = { ...this.query, pageSize: pageSize }
      this.toQuery()
    },
    handleClick(index) {
      // this.index = index
      this.drawerVisible = true
      this.query.education=index
      this.toQuery()
    },
  }
}
</script>

<style lang="scss" scoped>
#detection-users {
  width: 642px;
  height: 289px;
  // background: url("~@/assets/images/title_ywzl.png") no-repeat;
  background: rgba($color: #072036, $alpha: 0.46);
  padding-top: 17px;
  border-top: 3px solid;
  border-image: linear-gradient(to left, #052348, rgba(171, 218, 255, 0.58)) 1;

  .users-body {
    padding: 15px 10px 0 18px;
    display: grid;
    grid-template-columns: repeat(3, 32%);
    // grid-template-rows: repeat(3, 33.33%);
    grid-row-gap: 30px;
    grid-column-gap: 10px;

    .data-item {
      height: 70px;
      display: flex;

      .users-img {
        width: 72px;
        height: 72px;
        margin-right: 10px;
      }

      .users-info {
        color: #dadfe4;
        display: flex;
        flex-direction: column;
        justify-content: space-around;

        .users-num-blue {
          color: #96ceff;
          font-size: 24px;
          font-weight: 550;
          margin-right: 10px;
        }

        .users-num-green {
          color: #14dfe0;
          font-size: 24px;
          font-weight: 550;
          margin-right: 10px;
        }
      }
    }
  }
}
</style>
