<template>
  <div id="data-detail">
    <div class="radio-tools">
      <div :class="radioType == 1 ? 'radio-item active' : 'radio-item'" @click="toggleType(1)">
        月
      </div>
      <div :class="radioType == 2 ? 'radio-item active' : 'radio-item'" @click="toggleType(2)">
        季度
      </div>
      <div :class="radioType == 3 ? 'radio-item active' : 'radio-item'" @click="toggleType(3)">
        年
      </div>
    </div>

    <div class="report-data outside" @click="handleClick(13)">
      <div class="report-text">
        <div><span class="report-num">
            <CountTo :startVal='startVal' :endVal="externalData" :duration='duration' separator="," />
          </span>份</div>
        <span class="report-title">外检报告数</span>
      </div>
    </div>
    <div class="report-data inside" @click="handleClick(11)">
      <div class="report-text">
        <div><span class="report-num">
            <CountTo :startVal='startVal' :endVal="internalData" :duration='duration' separator="," />
          </span>份</div>
        <span class="report-title">内检报告数</span>
      </div>
    </div>
    <div class="report-data account" @click="handleClick(12)">
      <div class="report-text">
        <div><span class="report-num">
            <CountTo :startVal='startVal' :endVal="totalData" :duration='duration' separator="," />
          </span>份</div>
        <span class="report-title">累计报告总数</span>
      </div>
    </div>
    <div class="report-data keyi" @click="handleClick(14)" style="filter: grayscale(100%)">
      <div class="report-text">
        <div>
          <span class="report-num" style="color:#959caa">
            <!-- <CountTo :startVal='startVal' :endVal="totalData" :duration='duration' separator="," /> -->
            0
          </span>份
        </div>
        <span class="report-title" style="color:#959caa">可疑性报告数</span>
      </div>
    </div>

    <div :class="'data-item' + ' ' + item.className" v-for="(item, index) in dataArr" :key="index" :style="item.style" @click="handleClick(item.id)">

      <img class="data-icon" :src="item.img">

      <div class="data-item-text">
        <div><span class="data-item-num">
            <CountTo :startVal='startVal' :endVal="item.count" :duration='duration' separator="," />
          </span>份</div>
        <span>{{ item.name }}</span>
      </div>
      <!-- <img src="../../../assets/images/img_bgck.png" style="position: absolute;z-index:0;"> -->
    </div>

    <el-drawer title="详情" :visible.sync="drawerVisible" size="1000px" append-to-body modal-append-to-body @close="() => {
      this.query = Object.assign({}, this.initQuery)
    }">
      <dv-loading v-if="Loading">数据加载中...</dv-loading>
      <div v-else>
        <el-table ref="table" :data="tableValue" tooltip-effect="dark" style="width: 100%;" v-if="index == 2">
          <el-table-column label="序号" type="index" width="70"></el-table-column>
          <el-table-column label="样品名称" prop="sampleName" />
          <el-table-column label="样品编号" prop="sampleNumber">
          </el-table-column>
          <el-table-column label="收样时间" prop="sampleReceiveTime" />
          <el-table-column label="收样人员" prop="receiverName" />
          <el-table-column label="主检部门" prop="mainDept" />
        </el-table>
        <el-table ref="table" :data="tableValue" tooltip-effect="dark" style="width: 100%;" v-if="index == 3 || index == 4">
          <el-table-column label="序号" type="index" width="70"></el-table-column>
          <el-table-column label="样品名称" prop="sampleName" />
          <el-table-column label="样品编号" prop="sampleNumber">
          </el-table-column>
          <el-table-column label="任务下发时间" prop="receiveTime" />
          <el-table-column label="检测参数" prop="parameterName" />
          <el-table-column label="任务状态" prop="taskStatus">
            <!-- <template slot-scope="scope">
            {{ $dictLabel('report_type', scope.row.reportType + "") }}
          </template> -->
          </el-table-column>
          <el-table-column label="执行部门" prop="mainDept" />
          <el-table-column label="任务下发时间" prop="receiveTime" v-if="index == 4" />
        </el-table>
        <el-table ref="table" :data="tableValue" tooltip-effect="dark" style="width: 100%;" v-if="index == 5 || index == 6 || index == 7">
          <el-table-column label="序号" type="index" width="70"></el-table-column>
          <el-table-column label="任务状态" prop="taskStatus">
          </el-table-column>
          <el-table-column label="样品编号" prop="sampleNumber" />
          <el-table-column label="样品形态" prop="sampleShape" />
          <el-table-column label="检测参数" prop="parameterName" />
          <el-table-column label="执行部门" prop="mainDept" />
          <el-table-column label="任务下发时间" prop="receiveTime" v-if="index == 5" />
          <!-- <el-table-column label="记录上传时间" prop="reportUploadTime" v-if="index == 6 || index == 7" /> -->
        </el-table>
        <el-table ref="table" :data="tableValue" tooltip-effect="dark" style="width: 100%;" v-if="index == 9 || index == 8">
          <el-table-column label="序号" type="index" width="70"></el-table-column>
          <el-table-column label="报告编号" prop="reportNumber">
          </el-table-column>
          <el-table-column label="样品名称" prop="sampleName" />
          <el-table-column label="样品编号" prop="sampleNumber" />
          <el-table-column label="样品形态" prop="sampleShape" />
          <el-table-column label="检测参数" prop="parameterName" />
          <el-table-column label="报告上传时间" prop="reportUploadTime" />
        </el-table>
        <el-table ref="table" :data="tableValue" tooltip-effect="dark" style="width: 100%;" v-if="index == 10 || index == 1">
          <el-table-column label="序号" type="index" width="70"></el-table-column>
          <el-table-column label="报告编号" prop="reportNumber">
          </el-table-column>
          <el-table-column label="所属部门" prop="mainDept" />
          <el-table-column label="委托单位" prop="entrustUnit" />
          <el-table-column label="项目名称" prop="projectName" />
          <el-table-column label="项目编号" prop="projectNumber" />
          <el-table-column label="入库时间" prop="inputTime" v-if="index == 1" />
          <!-- <el-table-column label="出库时间" prop="outputTime" v-if="index == 1" /> -->
        </el-table>
        <el-table ref="table" :data="tableValue" tooltip-effect="dark" style="width: 100%;" v-if="index > 10 && index !== 14">
          <el-table-column label="序号" type="index" width="70"></el-table-column>
          <el-table-column label="报告编号" prop="reportNumber">
          </el-table-column>
          <el-table-column label="报告状态" prop="reportStatus" />
          <el-table-column label="检测指标" prop="parameterName" />
          <el-table-column label="项目名称" prop="projectName" />
          <el-table-column label="委托时间" prop="entrustDate" />
          <el-table-column label="收样时间" prop="sampleReceiveTime" />
        </el-table>
        <el-table ref="table" :data="tableValue" tooltip-effect="dark" style="width: 100%;" v-if="index == 14">
          <el-table-column label="序号" type="index" width="50"></el-table-column>
          <el-table-column label="报告编号" prop="reportNumber" />
          <el-table-column label="报告状态" prop="reportStatus" />
          <el-table-column label="可疑原因" prop="dubietyReason" />
          <el-table-column label="问题跟进" prop="problemFollow" />
          <el-table-column label="检测指标" prop="testIndex" />
          <el-table-column label="判定类型" prop="decideType" />
          <el-table-column label="判定时间" prop="decideDate" />
          <el-table-column label="完成时间" prop="accomplishDate" />
        </el-table>
        <div class="table-pagination">
          <div class="data-num">共<span> {{ query.total }} </span>条数据</div>
          <el-pagination :current-page="query.pageIndex" :page-size="query.pageSize" :total="query.total" background layout="sizes, prev, pager, next, jumper" @size-change="handleSizeChange"
            @current-change="paginationCurrentChange" />
        </div>
      </div>
    </el-drawer>
  </div>
</template>

<script>
import {
  TaskProcessApi,
  queryExternalApi,
  queryInternalApi,
  queryTotalReportApi,
  DetectionReportListApi,
  getDubietyReportPage,
} from "@/api/detection.js";
import CountTo from "vue-count-to";

import bgck from "../../../assets/images/icon_bgck.png";
import wtsy from "../../../assets/images/icon_wtsy.png";
import rwfp from "../../../assets/images/icon_rwfp.png";
import rwqr from "../../../assets/images/icon_rwqr.png";
import syjl from "../../../assets/images/icon_syjl.png";
import jlsh from "../../../assets/images/icon_jlsh.png";
import bgcj from "../../../assets/images/icon_bgcj.png";
import bgsh from "../../../assets/images/icon_bgsh.png";
import bgpz from "../../../assets/images/icon_bgpz.png";
import bgrk from "../../../assets/images/icon_bgrk.png";

export default {
  data() {
    this.initQuery = {
      pageIndex: 1,
      pageSize: 10,
      total: 0,
    };
    return {
      dataArr: [
        {
          id: 1,
          name: "报告出库",
          count: 0,
          className: "bgck",
          img: bgck,
          style: { color: "#FFD7A5", left: "250px", top: "60px" },
        },
        {
          id: 2,
          name: "委托收样",
          count: 0,
          className: "wtsy",
          img: wtsy,
          style: { color: "#84D0FF", left: "520px", top: "30px" },
        },
        {
          id: 3,
          name: "任务分配",
          count: 0,
          className: "rwfp",
          img: rwfp,
          style: { color: "#FFFEDD", left: "760px", top: "50px" },
        },
        {
          id: 4,
          name: "任务确认",
          count: 0,
          className: "rwqr",
          img: rwqr,
          style: { color: "#77E5DD", left: "980px", top: "130px" },
        },
        {
          id: 5,
          name: "试验记录",
          count: 0,
          className: "syjl",
          img: syjl,
          style: { color: "#84D0FF", left: "1030px", top: "320px" },
        },
        {
          id: 6,
          name: "记录审核",
          count: 0,
          className: "jlsh",
          img: jlsh,
          style: { color: "#FFD7A5", left: "850px", top: "410px" },
        },
        {
          id: 7,
          name: "报告出具",
          count: 0,
          className: "bgcj",
          img: bgcj,
          style: { color: "#84D0FF", left: "580px", top: "450px" },
        },
        {
          id: 8,
          name: "报告审核",
          count: 0,
          className: "bgsh",
          img: bgsh,
          style: { color: "#FFFEDD", left: "300px", top: "430px" },
        },
        {
          id: 9,
          name: "报告批准",
          count: 0,
          className: "bgpz",
          img: bgpz,
          style: { color: "#84D0FF", left: "50px", top: "370px" },
        },
        {
          id: 10,
          name: "报告入库",
          count: 0,
          className: "bgrk",
          img: bgrk,
          style: { color: "#77E5DD", left: "50px", top: "150px" },
        },
      ],
      radioType: 1,
      startVal: 0,
      duration: 1000,
      externalData: 0,
      internalData: 0,
      totalData: 0,
      query: Object.assign({}, this.initQuery),
      drawerVisible: false,
      tableValue: [],
      index: 0,
      Loading: false,
    };
  },
  components: {
    CountTo,
  },
  mounted() {
    this.initChart();
  },
  methods: {
    handleClick(index) {
      this.index = index;
      this.drawerVisible = true;
      this.toQuery();
    },
    toQuery() {
      this.Loading = true;
      if (this.index == 1) {
        this.query = { ...this.query, queryType: "报告出库" };
      }
      if (this.index == 2) {
        this.query = { ...this.query, queryType: "委托收样" };
      }
      if (this.index == 3) {
        this.query = { ...this.query, queryType: "任务分配" };
      }
      if (this.index == 4) {
        this.query = { ...this.query, queryType: "任务确认" };
      }
      if (this.index == 5) {
        this.query = { ...this.query, queryType: "试验记录" };
      }
      if (this.index == 6) {
        this.query = { ...this.query, queryType: "记录审核" };
      }
      if (this.index == 7) {
        this.query = { ...this.query, queryType: "报告出具" };
      }
      if (this.index == 8) {
        this.query = { ...this.query, queryType: "报告审核" };
      }
      if (this.index == 9) {
        this.query = { ...this.query, queryType: "报告批准" };
      }
      if (this.index == 10) {
        this.query = { ...this.query, queryType: "报告入库" };
      }
      if (this.index == 11) {
        this.query = { ...this.query, queryType: "内检报告数" };
      }
      if (this.index == 12) {
        this.query = { ...this.query, queryType: "累计报告数" };
      }
      if (this.index == 13) {
        this.query = { ...this.query, queryType: "外检报告数" };
      }
      let getApi =
        this.index == 14 ? getDubietyReportPage : DetectionReportListApi;
      getApi({ ...this.query, dateType: this.radioType })
        .then((res) => {
          if (res.code == 0) {
            this.Loading = false;
            this.tableValue = res.data.records;
            this.query.total = res.data.total;
          }
        })
        .finally(() => {
          this.Loading = false;
        });
    },
    paginationCurrentChange(pageIndex) {
      this.query = { ...this.query, pageIndex: pageIndex };
      this.toQuery();
    },
    handleSizeChange(pageSize) {
      this.query = { ...this.query, pageSize: pageSize };
      this.toQuery();
    },
    toggleType(type) {
      this.radioType = type;
      this.initChart();
    },

    initChart() {
      TaskProcessApi(this.radioType).then((res) => {
        const { code, data } = res;
        if (code == 0) {
          // console.log(data)
          data.forEach((item) => {
            this.dataArr.forEach((ele) => {
              if (ele.name == item.name) {
                ele.count = item.value;
              }
            });
          });
        }
      });
      queryExternalApi(this.radioType).then((res) => {
        const { code, data } = res;
        if (code == 0) {
          this.externalData = data;
        }
      });

      queryInternalApi(this.radioType).then((res) => {
        const { code, data } = res;
        if (code == 0) {
          this.internalData = data;
        }
      });

      queryTotalReportApi(this.radioType).then((res) => {
        const { code, data } = res;
        if (code == 0) {
          this.totalData = data;
        }
      });
    },
  },
};
</script>

<style lang="scss" scoped>
#data-detail {
  width: 1270px;
  height: 630px;
  // background: url("~@/assets/images/img_bg.png") no-repeat bottom;
  position: relative;

  .radio-tools {
    display: flex;
    position: absolute;
    right: 24px;

    .radio-item {
      width: 74px;
      height: 32px;
      line-height: 32px;
      text-align: center;
      background-color: rgba(13, 123, 180, 0.5);
      border: 1px solid #0986c6;

      &:hover {
        cursor: pointer;
        background-color: rgba(255, 175, 20, 0.5);
        border: 1px solid #ffaf14;
      }
    }

    .radio-item.active {
      background-color: rgba(255, 175, 20, 0.5);
      border: 1px solid #ffaf14;
    }
  }

  .report-data {
    width: 209px;
    height: 225px;
    position: absolute;
    background: url("~@/assets/images/img_zhuzi.png") no-repeat;

    .report-text {
      color: #c4d2db;
      font-size: 14px;
      position: absolute;
      left: 20px;
      top: -15px;
      line-height: 22px;
      text-align: center;
      width: 180px;
      font-weight: 450;

      position: absolute;
      z-index: 9999;

      .report-title {
        font-weight: 520;
      }

      .report-num {
        color: #fff;
        font-size: 28px;
        margin-right: 5px;
      }
    }
  }

  .report-data.outside {
    top: 240px;
    left: 267px;
  }

  .report-data.inside {
    top: 280px;
    left: 460px;
  }

  .report-data.account {
    top: 220px;
    left: 813px;
  }

  .report-data.keyi {
    top: 220px;
    left: 623px;
  }

  .data-item {
    position: absolute;
    width: 213px;
    height: 192px;

    .data-icon {
      position: absolute;
      top: 70px;
      left: 85px;

      animation-name: beat;
      animation-duration: 1s;
      /*设置秒数*/
      animation-timing-function: linear;
      /*速度曲线*/
      animation-iteration-count: infinite;
      /*播放次数*/
      animation-direction: alternate;
      /*逆向播放*/
      animation-play-state: running;
    }

    @keyframes beat {
      0% {
        transform: translate(0, 0);
      }

      100% {
        transform: translate(0px, 10px);
        // transform: scale(1.2);
      }
    }

    .data-item-text {
      position: absolute;
      top: 18px;
      left: 10px;
      line-height: 22px;
      font-size: 14px;
      font-family: PingFangSC-Medium, PingFang SC;
      // display: flex;
      // flex-direction: column;
      // align-items: center;
      width: 200px;

      text-align: center;

      .data-item-num {
        font-size: 24px;
        font-weight: 550;
        margin-right: 10px;
      }
    }
  }

  .data-item.bgck {
    background: url("~@/assets/images/img_bgck.png") no-repeat;
  }

  .data-item.wtsy {
    background: url("~@/assets/images/img_wtsy.png") no-repeat;
  }

  .data-item.rwfp {
    background: url("~@/assets/images/img_rwfp.png") no-repeat;
  }

  .data-item.rwqr {
    background: url("~@/assets/images/img_rwqr.png") no-repeat;
  }

  .data-item.syjl {
    background: url("~@/assets/images/img_syjl.png") no-repeat;
  }

  .data-item.jlsh {
    background: url("~@/assets/images/img_jlsh.png") no-repeat;
  }

  .data-item.bgcj {
    background: url("~@/assets/images/img_bgcj.png") no-repeat;
  }

  .data-item.bgsh {
    background: url("~@/assets/images/img_bgsh.png") no-repeat;
  }

  .data-item.bgpz {
    background: url("~@/assets/images/img_bgpz.png") no-repeat;
  }

  .data-item.bgrk {
    background: url("~@/assets/images/img_bgrk.png") no-repeat;
  }
}
</style>
